import React, { useEffect, useState } from 'react'
import './style.css'
import * as backimgtop from '../../assets/Clean Walk/Levels_Screen/BG_top.svg';
import * as lslogo from '../../assets/images/Levels_Screen/LS_girl.png';

import * as scanback from '../../assets/Clean Walk/Levels_Screen/LS_qr_base.png'
import * as lsqr from '../../assets/images/Levels_Screen/LS_qr.png'
import * as lv9img from '../../assets/images/Levels_Screen/LS_dot1.png'
import * as lv10img from '../../assets/images/Levels_Screen/LS_dot2.png'

import { useHistory } from 'react-router-dom'
import { loader } from 'components/Common/core';
import Spinner from 'components/Common/Spinner';
import HeaderComponent from 'components/Common/HeaderComponent';

import { levelstore } from '../../store/levelStore';


const listItems = [
  {
    "TaskLevel": "1",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_01_on.png",
    "active": true,
  },
  {
    "TaskLevel": "2",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_02_on.png",
    "active": true,
  },
  {
    "TaskLevel": "3",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_03_on.png",
    "active": false,
  },
  {
    "TaskLevel": "4",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_04_on.png",
    "active": false,
  },
  {
    "TaskLevel": "5",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_05_on.png",
    "active": true,
  },
  {
    "TaskLevel": "6",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_06_on.png",
    "active": false,
  },
  {
    "TaskLevel": "7",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_07_on.png",
    "active": false,
  },
  {
    "TaskLevel": "8",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_08_on.png",
    "active": false,
  },
  {
    "TaskLevel": "9",
    "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/CLUES/Clue_lev_09.png",
    "active": false,
  },
]



const TaskListScreen = () => {
  const { level, loadlevel } = levelstore();

  const [list, setlist] = useState(listItems)
  const history = useHistory();
  const [spinner, setspinner] = useState(true)

  const [levelicon, setlevelicon] = useState('')

  const navigate = (id: number) => {
    history.replace('/TaskScreen/' + id)
  }

  useEffect(() => {

    if (!level) {
      loadlevel()
    }

    if (localStorage.getItem('isFirsttime') === null) {
      loader()
    } setspinner(false)
    let a = JSON.parse(String(localStorage.getItem('EventStatusData')))
    let temp = []
    if (a !== null) {
      a.forEach((ele: any) => {
        if (ele.eventType === "Task")
          listItems.forEach(ele1 => {
            if (ele1.TaskLevel === ele.Level) {
              let abc = {
                TaskLevel: ele1.TaskLevel,
                image: ele1.image,
                active: false
              }
              if (ele.Status === "LOCKED") {
                abc.active = false
              } else {
                abc.active = true
              }
              temp.push(abc)
            }
          });
      });
    }
    setlist(temp)
    console.log(temp)

  }, [])

  const loadicon = (levelid: any) => {
    let a = "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/CLUES/Clue_lev_09.png";
    (level) && level.map((obj: any) => {
      // console.log(">> title >> : ", title, obj.l_title);

      if (String(levelid) === String(obj.l_level)) {
        // console.log(">> obj.l_icon >> : ", obj.l_icon);
        a = obj.l_icon
      }
    })
    return a
  }

  return (
    <div className="back_color_main">
      {spinner ?
        <div style={{
          width: '100vw',
          height: '100vh',
          position: 'absolute',
          top: '0',
          zIndex: 1000,
          backgroundColor: '#00243a',
        }}>
          <Spinner />
        </div> :
        null}
      <>
        <div>
          <img className='bgimgtop' src={backimgtop} />
        </div>
        <HeaderComponent />

        <div className='task_scanqrcontainer_main'>
          <div className='task_scanqrcontainer_sub'>
            <img className='task_scanback' src={scanback} />
            <div
              onClick={() => {
                history.replace('/ClueListScreen')
              }}
              className='task_scanqrcontainer'>
              <img className='task_scanqr' src={lsqr} />
            </div>
          </div>
        </div>

        <div className='taskslist_container'>
          <div className='taskslist'>

            <img className='tasklslogo' src={lslogo} />
            {/* {list[7].active ? */}
            <div className='taskicon11' onClick={() => {
              navigate(8)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(8)} />
            </div>
            {/* :
              <div className='taskicon11'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(8)} />
              </div>
            } */}
            {/* {list[6].active ? */}
            <div className='taskicon10' onClick={() => {
              navigate(7)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(7)} />
            </div>
            {/* :
              <div className='taskicon10'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(7)} />
              </div>} */}
            {/* {list[5].active ? */}
            <div className='taskicon9' onClick={() => {
              navigate(6)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(6)} />
            </div>
            {/* :
              <div className='taskicon9'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(6)} />
              </div>} */}

            <div className='taskicon8'>
              <img className='tasklevelimagesmalldisabled' src={lv9img} />
            </div>

            {/* {list[4].active ? */}
            <div className='taskicon7' onClick={() => {
              navigate(5)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(5)} />
            </div>
            {/* :
              <div className='taskicon7'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(5)} />
              </div>
            } */}

            <div className='taskicon6'>
              <img className='tasklevelimageExsmalldisabled' src={lv10img} />
            </div>



            {/* {list[3].active ? */}
            <div className='taskicon4' onClick={() => {
              navigate(4)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(4)} />
            </div>
            {/* :
              <div className='taskicon4'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(4)} />
              </div>} */}

            {/* {list[2].active ? */}
            <div className='taskicon3'
              onClick={() => {
                navigate(3)
              }}>
              <img className='tasklevelimagemedium' src={loadicon(3)} />
            </div>
            {/* :
              <div className='taskicon3'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(3)} />
              </div>
            } */}

            {/* {list[1].active ? */}
            <div className='taskicon2' onClick={() => {
              navigate(2)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(2)} />
            </div>
            {/* :
              <div className='taskicon2'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(2)} />
              </div>
            } */}

            {/* {list[0].active ? */}
            <div className='taskicon1' onClick={() => {
              navigate(1)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(1)} />
            </div>
            {/* :
              <div className='taskicon1'>
                <img className='tasklevelimagemediumdisabled' src={loadicon(1)} />
              </div>
            } */}

          </div>
        </div>

        <div className="bgbottombluebar" />

      </>


    </div>
  )
}


export default TaskListScreen
